import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Events } from '@ionic/angular';


import { ItemsSandbox } from '@sandboxes/items.sandbox';
import { CatalogSandbox } from '@sandboxes/catalog.sandbox';

import { ICatalog } from '@store/models/catalog.model';
import { IItem } from '@store/models/items.model';
import { IUser, UserTypeEnum } from '@store/models/user.model';
import { UserSandbox } from '@sandboxes/user.sandbox';

@Component({
  selector: 'app-items',
  templateUrl: './items.page.html',
  styleUrls: ['./items.page.scss'],
})
export class ItemsPage implements OnInit {

  subscription;
  item: IItem;
  items: Array<IItem> = [];
  catalog: ICatalog;
  name: string;
  isMerchant: boolean;

  constructor(private router: Router,
              private itemSandbox: ItemsSandbox,
              private catalogSandbox: CatalogSandbox,
              private userSandbox: UserSandbox
  ) {
  }

  ngOnInit() {
    this.subscription = this.itemSandbox.itemErrorMessage$.subscribe((error: string) => {
      // if (error) {
      //   this.toast.show(error, '3000', 'center');
      // }
    });
    this.itemSandbox.currentItem$.subscribe((item: IItem) => {
      this.item = item;
    });
    this.itemSandbox.items$.subscribe((items: IItem[]) => {
      this.items = items;
    });
    this.catalogSandbox.currentCatalog$.subscribe((catalog: ICatalog) => {
      if (catalog._id) {
        this.catalog = catalog;
      }
    });
    this.userSandbox.user$.subscribe((user: IUser) => {
      this.name = user.companyName;
      this.isMerchant = user.role === UserTypeEnum.MERCHANT;
    });
  }

  selectItem(item: IItem) {
    this.itemSandbox.setCurrentItem(item);
    this.router.navigateByUrl('/app/tabs/catalogs/editItem');
  }

  onCancel() {
    this.router.navigateByUrl('/app/tabs/catalogs');
  }

  onNewItem() {
    console.log('new item');
  }
}
